<template>
  <div class="head">
    <h3 class="head_title"><router-link to="/" class="head_title_a">{{blog_title}}</router-link></h3>
    <div class="wrapper">
      <div v-if="!isLogin" class="wrapper-text">
        <router-link class="text" to="/login">登录</router-link>
        <router-link class="text" to="/register">注册</router-link>
      </div>
      <div class="wrapper-img" v-if="isLogin">
        <img @click="showMenu" class="img headimg" src="http://via.placeholder.com/40x40" alt="">
        <ul class="img_menu" v-show="isShowMenu">
          <li class="item">个人中心</li>
          <li class="item">设置</li>
          <li class="item" @click="loginOut">退出登录</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import AV from 'leancloud-storage'

  export default {
    props: {
      isLogin: {
        type: Boolean,
        default: false
      },
      blog_title: {
        type: String,
        default: 'blog_title'
      }
    },
    data() {
      return {
        isShowMenu: false
      }
    },
    mounted() {

    },
    methods: {
      showMenu() {
        this.isShowMenu = true
      },
      loginOut() {
        AV.User.logOut();
        this.isLogin = false;
      },


    }
  }
</script>

<style scoped>

  .head {
    background: #419fff;
    color: #fff;
    height: 60px;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
  }

  .head_title {
    font-size: 20px;
    width: 100%;
    text-align: center;
  }

  .head_title_a {
    padding: 10px;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
  }

  .text {
    text-decoration: none;
    color: #fff;
    margin-left: 20px;
  }

  .wrapper-img {
    position: relative;
  }

  .img {
    border-radius: 100%;
    margin-top: 8px;
    margin-right: 100px;
    cursor: pointer;
  }

  .img_menu {
    position: absolute;
    left: -40px;
    top: 60px;
    color: #333;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0px 0px 5px #aaa;
    width: 120px;
  }

  .img_menu .item {
    line-height: 28px;
    font-size: 14px;
    cursor: pointer;
  }

  .img_menu .item:hover {
    background: #419fff;
    color: #fff;
  }

  .wrapper-text {
    width: 300px;
  }
</style>



