<template>
  <div class="detail">
    <h2 class="detail-title">{{this.result.attributes.title}}</h2>
    <div class="base-info">
      <div>
        <img class="img" src="http://via.placeholder.com/40x40" alt="">
      </div>
      <div>
        <p class="nickname">suxing</p>
        <p class="other">2018.3.6 16:69</p>
      </div>
    </div>
    <div class="content" v-html="this.result.attributes.content"></div>
  </div>
</template>

<script>
  import AV from 'leancloud-storage'

  export default {
    data() {
      return {
        id: '',
        result: ''
      }
    },
    mounted() {
      let context = this;
      this.id = this.$route.query.id;
      var query = new AV.Query('Article');
      query.get(this.id).then(function (result) {
        console.log(result);
        context.result = result;
      }, function (err) {
        console.log(err)
      })
    }
  }
</script>

<style scoped>
  .detail {
    padding: 0 300px 100px 300px;
    text-align: left;
  }

  .detail-title {
    margin-top: 60px;

  }

  .base-info {
    display: flex;
    margin-top: 30px;
  }
  .img{
    border-radius: 100%;
    margin-right: 14px;
  }
  .other{
    font-size: 14px;
    color: #949494;
    padding-top: 8px;
  }
  .content{
    margin-top: 30px;
  }
</style>



