<template>
  <div class="article">
    <div class="home-item home-left">
      <div class="home-top">
        <edit-text :fontClass="fontClass_search" :textType="textType_search" :hint="hint_search"
                   @on-result-change="onResultChangeSearch"></edit-text>
        <my-button :size="size" :value="value"></my-button>
        <my-button :click="to_publish" :fontClass="fontClass_add" class="top-item" :size="size"
                   :value="value_add"></my-button>
      </div>
      <list-view :dataList="dataList"></list-view>
    </div>
    <div class="home-item home-right">
      <div class="menu by-date">
        <h3 class="menu-title">按日期筛选</h3>
        <ul class="by-date-list">
          <li class="list-item">2018年4月</li>
          <li class="list-item">2018年2月</li>
          <li class="list-item">2018年1月</li>
        </ul>
      </div>
      <div class="menu by-class">
        <h3 class="menu-title">按分类筛选</h3>
        <ul class="by-class-list">
          <li class="list-item">js</li>
          <li class="list-item">css</li>
          <li class="list-item">html</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import EditText from 'base/edittext/EditText'
  import MyButton from 'base/button/Button'
  import ListView from 'base/listview/ListView'
  import AV from 'leancloud-storage'

  export default {
    data() {
      return {
        fontClass_search: 'iconfont icon-sousuo',
        fontClass_add: 'iconfont icon-tianjiajiahaowubiankuang',
        textType_search: 'text',
        hint_search: '搜索',
        search: '',
        value: '搜索',
        size: 'larger',
        value_add: '发表',
        dataList:''
      }
    },
    components: {
      EditText,
      MyButton,
      ListView
    },
    mounted() {
      let context = this;
      let query = new AV.Query('Article');
      query.descending('createAt');
      query.find()
        .then(function (result) {
          console.log(result)
          context.dataList = result
        }).catch(function (err) {
        console.log(JSON.stringify(err))
      })
    },
    methods: {
      onResultChangeSearch(val) {
        this.search = val
      },
      to_publish() {
        this.$router.push({path: '/home/publish'})
      }
    }
  }
</script>

<style scoped>
  .article {
    display: flex;
    justify-content: space-between;
  }

  .home-left {
    width: 80%;
    padding: 30px 100px 0 100px;
  }

  .home-top {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
  }

  .top-item {
    grid-column: 4/6;
  }

  .menu {
    width: 160px;
  }

  .home-right {
    width: 20%;
    padding-top: 30px;
    padding-left: 40px;
  }

  .menu-title {
    padding: 10px 0;
    text-align: left;
  }

  .by-date {
    padding-bottom: 20px;
  }

  .by-class {
    margin-top: 30px;
  }

  .list-item {
    text-align: left;
    cursor: pointer;
    padding: 5px 8px;
  }

  .list-item:hover {
    background: #f0f0f0;
  }
</style>



