<template>
  <div>
    <div class="form">
      <edit-text :fontClass="fontClass_user" :textType="textType_user" :hint="hint_user"
                 @on-result-change="onResultChangeUser"></edit-text>
      <edit-text :fontClass="fontClass_paw" :textType="textType_paw" :hint="hint_paw"
                 @on-result-change="onResultChangePaw"></edit-text>
      <edit-text :fontClass="fontClass_paw" :textType="textType_repaw" :hint="hint_repaw"
                 @on-result-change="onResultChangeRePaw"></edit-text>
      <edit-text :fontClass="fontClass_nickname" :textType="textType_ninckname" :hint="hint_nickname"
                 @on-result-change="onResultChangeNickName"></edit-text>
      <my-button :click="to_register" :size="size" :value="value"></my-button>
      <toast :isShowToast="isShowToast" :text="toast_text"></toast>
    </div>
  </div>
</template>


<script>
  import EditText from 'base/edittext/EditText'
  import MyButton from 'base/button/Button'
  import Toast from 'base/toast/Toast'
  import Regular from '../../api/regular.js'
  import AV from 'leancloud-storage'

  AV.init('xqIvIKnYLIcAbE8XiktLGiRB-gzGzoHsz',
    'ts9zN6qUeQrFa9V8Q5vngCGj')

  export default {
    data() {
      return {
        size: 'larger',
        value: '注册',
        username: '',
        password: '',
        repassword: '',
        nickname: '',
        hint_user: '请输入邮箱',
        hint_paw: '请输入密码',
        hint_repaw: '请确认密码',
        hint_nickname: '请确认昵称',
        textType_user: 'text',
        textType_paw: 'password',
        textType_repaw: 'password',
        textType_ninckname: 'text',
        fontClass_user: 'iconfont icon-mail',
        fontClass_paw: 'iconfont icon-icon-',
        fontClass_nickname: 'iconfont icon-yonghu1',
        toast_text: '',
        isShowToast: false,
      }
    },
    components: {
      EditText,
      MyButton,
      Toast
    },
    mounted() {

    },
    methods: {
      to_register() {
        if (this.username === '' || typeof this.username === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '邮箱不能为空';
          return
        } else {
          this.isShowToast = false;
        }
        if (!Regular.checkEmail(this.username)) {
          this.isShowToast = true;
          this.toast_text = '邮箱格式不正确';
          return
        } else {
          this.isShowToast = false;
        }
        if (this.password === '' || typeof this.password === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '密码不能为空';
          return
        } else {
          this.isShowToast = false;
        }
        if (this.repassword !== this.password) {
          this.isShowToast = true;
          this.toast_text = '两次密码不一致';
          return
        } else {
          this.isShowToast = false;
        }
        if (this.nickname === '' || typeof this.nickname === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '昵称不能为空';
        } else {
          this.isShowToast = false;
        }

        var user = new AV.User();
        user.setUsername(this.username);
        user.setPassword(this.password);
        user.setEmail(this.username);
        user.signUp().then(function (loginedUser) {
          console.log(loginedUser)
        }, function (err) {
          console.log(err)
        })
        // AV.User.requestEmailVerify(this.username)
        //   .then(function (result) {
        //     console.log(JSON.stringify(result))
        //   },function (err) {
        //     console.log(JSON.stringify(err))
        //   })

      },
      onResultChangeUser(val) {
        this.username = val
      },
      onResultChangePaw(val) {
        this.password = val
      },
      onResultChangeRePaw(val) {
        this.repassword = val
      },
      onResultChangeNickName(val) {
        this.nickname = val
      }
    }
  }
</script>

<style scoped>
  .form {
    width: 300px;
    margin: 100px auto;
  }
</style>



