<template>
  <div>
    <div class="form">
      <edit-text :fontClass="fontClass_user" :textType="textType_user" :hint="hint_user"
                 @on-result-change="onResultChangeUser"></edit-text>
      <edit-text :fontClass="fontClass_paw" :textType="textType_paw" :hint="hint_paw"
                 @on-result-change="onResultChangePaw"></edit-text>
      <my-button :click="to_login" :size="size" :value="value"></my-button>
      <toast :isShowToast="isShowToast" :text="toast_text"></toast>
    </div>
  </div>
</template>

<script>
  import EditText from 'base/edittext/EditText'
  import MyButton from 'base/button/Button'
  import Toast from 'base/toast/Toast'
  import Regular from '../../api/regular.js'
  import AV from 'leancloud-storage'
  import bus from "../../common/js/bus";
  import localStorageApi from '../../api/localstorage'

  export default {
    data() {
      return {
        size: 'larger',
        value: '登录',
        username: '',
        password: '',
        hint_user: '请输入邮箱',
        hint_paw: '请输入密码',
        textType_user: 'text',
        textType_paw: 'password',
        fontClass_user: 'iconfont icon-mail',
        fontClass_paw: 'iconfont icon-icon-',
        toast_text: '',
        isShowToast: false,
      }
    },
    components: {
      EditText,
      MyButton,
      Toast
    },
    methods:{
      onResultChangeUser(val) {
        this.username = val
      },
      onResultChangePaw(val) {
        this.password = val
      },
      to_login(){
        if (this.username === '' || typeof this.username === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '邮箱不能为空';
          return
        } else {
          this.isShowToast = false;
        }
        if (!Regular.checkEmail(this.username)) {
          this.isShowToast = true;
          this.toast_text = '邮箱格式不正确';
          return
        } else {
          this.isShowToast = false;
        }
        if (this.password === '' || typeof this.password === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '密码不能为空';
        } else {
          this.isShowToast = false;
        }
        let context = this;
        AV.User.logIn(this.username,this.password)
          .then(function (loginedUser) {
            // localStorageApi.setData('blog',{
            //   username:this.username,
            //   password:this.password,
            //   id:loginedUser.id
            context.isShowToast = false;
            bus.$emit('isLogin', true)
            context.$router.push({path:'/home'})
            console.log(loginedUser)
          },function (err) {
            context.isShowToast = true;
            context.toast_text = '登录失败，检查账号和密码是否正确';
            console.log(1)
            console.log(err.toString())
          })
      }
    }
  }
</script>

<style scoped>
  .form {
    width: 300px;
    margin: 100px auto;
  }
</style>



