<template>
  <div class="listView">
    <!--<router-link class="item_a" to="/detail" v-for="(item,index) in dataList">-->
    <!--<div class="item">-->
    <!--<div class="list_title">{{item.title}}</div>-->
    <!--<div class="list_content">{{item.content}}</div>-->
    <!--</div>-->
    <!--</router-link>-->
    <div class="item" v-for="(item,index) in dataList">
      <div class="item-top">
        <img class="img" src="http://via.placeholder.com/40x40" alt="">
        <span class="nickname">s素心uxing</span>
        <span class="date">{{item.attributes.date}}</span>
      </div>
      <h3 class="item-title"><router-link class="item-title_a" :to="{path:'/home/detail/',query:{id:item.id}}">{{item.attributes.title}}</router-link></h3>
      <div class="content" v-html="item.attributes.content"></div>
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      dataList: {
        type: Array,
        default: []
      }
    }
  }
</script>

<style scoped>
  .item-title_a{
    color: #333;
    text-decoration: none;
  }
  .item-title:hover{
    text-decoration: underline;
  }
  .item{
    width: 670px;
    text-align: left;
    margin-top: 10px;
    margin-bottom: 30px;
    padding-bottom: 16px;
    border-bottom: 1px solid #ccc;
  }
  .item-top {
    display: flex;
    align-items: center;
  }
  .item-title{
    padding: 16px 0 ;
    font-size: 24px;
  }

  .img {
    border-radius: 100%;
  }

  .nickname {
    margin-left: 16px;
    font-size: 16px;
  }

  .date {
    margin-left: 12px;
    font-size: 14px;
    color: #949494;
  }
  .content{
    width: 670px;
    height: 60px;
    line-height: 20px;
    font-size: 16px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
</style>



